




























import { Component, Watch, Prop, Vue } from "vue-property-decorator";
import Table from "../model";

@Component({
  // other options
  filters: {
    capitalize: function(value) {
      if (!value) return "";
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    }
  }
})
export default class Grid extends Vue {
  @Prop({ required: true })
  private tableData!: Table;

  @Prop()
  private filterKey!: string;

  sortKey = "";
  mutableTableData = this.tableData;

  sortOrders(key: string) {
    this.sortOrders[key] = 1;
    return this.sortOrders;
  }

  get filteredData() {
    const filterKey = this.filterKey && this.filterKey.toLowerCase();
    const order = this.sortOrders[this.sortKey] || 1;
    let rowsData = this.tableData.rows;
    if (filterKey) {
      rowsData = rowsData.filter(function(row) {
        return Object.keys(row).some(function(key) {
          return (
            String(row[key])
              .toLowerCase()
              .indexOf(filterKey) > -1
          );
        });
      });
    }
    if (this.sortKey) {
      this.tableData.rows = this.tableData.rows
        .slice()
        .sort((a: string, b: string) => {
          a = a[this.sortKey];
          b = b[this.sortKey];
          return (a === b ? 0 : a > b ? 1 : -1) * order;
        });
    }
    return rowsData;
  }

  sortBy(key: string) {
    this.sortKey = key;
    this.sortOrders[key] = this.sortOrders[key] * -1;
  }
}
